@import url(https://fonts.googleapis.com/css2?family=Ibarra+Real+Nova&family=Public+Sans&display=swap);
:root{--slightlyDesaturatedCyan: #5fb4a2;--darkBlue: #203a4c;--grayishDarkBlue: #33323d;--bgColor: #fafafa;--lightGray: #eaeaeb;--red: #f43030;--headingsFont: 'Ibarra Real Nova', serif}body{margin:0;padding:0;box-sizing:border-box;color:#33323d;color:var(--grayishDarkBlue);font-family:'Public Sans', sans-serif;font-size:16px;line-height:30px;background-color:#fafafa;background-color:var(--bgColor)}h1,h2,h3{color:#33323d;color:var(--grayishDarkBlue)}h1{font-family:'Ibarra Real Nova', serif;font-family:var(--headingsFont);font-weight:bold;font-size:50px;line-height:50px;letter-spacing:-0.45px}h2{font-family:'Ibarra Real Nova', serif;font-family:var(--headingsFont);font-weight:bold;font-style:normal;font-size:40px;line-height:42px;letter-spacing:-0.36px}h3{font-family:'Ibarra Real Nova', serif;font-family:var(--headingsFont);font-weight:normal;font-size:32px;line-height:36px;letter-spacing:-0.29px}.btn{display:inline-block;padding:17px 36px;border:1px solid #33323d;border:1px solid var(--grayishDarkBlue);font-style:normal;font-weight:normal;font-size:12px;line-height:14px;letter-spacing:2px;text-transform:uppercase;text-decoration:none;color:#33323d;color:var(--grayishDarkBlue)}.btn--dark{background:#203a4c;background:var(--darkBlue);color:#fff}

.container{width:80%;margin:0 auto}@media (min-width: 768px){.container{width:90%}}@media (min-width: 992px){.container{max-width:1100px}}

.hero__title{margin:0;margin-top:24px;margin-bottom:32px}.hero img{display:block;width:100%}.hero__btn{position:relative;left:48px;display:inline-block;width:152px;height:48px;padding:0;border:none;background-color:#203a4c;color:#fff;font-style:normal;font-weight:normal;font-size:12px;line-height:48px;text-align:center;letter-spacing:2px;text-transform:uppercase;text-decoration:none}.hero__btn::before{content:'';position:absolute;left:-48px;top:0;display:inline-block;height:48px;width:48px;background:#000 url(/portfolio/static/media/down-arrows.2bd23bfb.svg) no-repeat;background-position:center}.about__img{margin-top:80px;margin-bottom:30px;width:100%}.about__content{padding-bottom:51px;border-top:1px solid rgba(0,0,0,0.2);border-bottom:1px solid rgba(0,0,0,0.2)}.about__text{margin-bottom:24px}@media (min-width: 768px){.hero{position:relative}.hero__title{box-sizing:border-box;position:absolute;bottom:0;left:0;width:514px;height:278px;background-color:#fafafa;margin:0;padding-top:56px;padding-right:56px}.hero__btn{position:absolute;bottom:0;left:48px}.about{display:flex;justify-content:space-between;margin-top:96px}.about__img{display:block;width:40%;margin:0}.about__content{width:50%}}@media (min-width: 992px){.hero{max-height:600px}.hero img{max-height:600px}.hero__title{width:445px;height:357px}.about{max-width:1015px}.about__img{width:50%}.about__content{width:40%}}

.contact{margin-top:115px;margin-bottom:80px;text-align:center;padding:0 10px}@media (min-width: 768px){.contact{display:flex;justify-content:space-between;align-items:center;margin-top:96px;padding:0}.contact__title{width:50%;text-align:left}.contact__line{width:15%;border:1px solid var(--grayishDarkBlue);opacity:0.15}}@media (min-width: 992px){.contact__title{width:30%;text-align:left}.contact__line{width:50%;border:1px solid var(--grayishDarkBlue);opacity:0.15}}


.project{margin-bottom:72px}.project__thumbnail{display:block;width:100%;margin-bottom:32px}.project__info{border-top:1px solid rgba(0,0,0,0.15);border-bottom:1px solid rgba(0,0,0,0.15);padding-bottom:24px}@media (min-width: 768px){.project{display:flex;justify-content:space-between;align-items:center;margin-bottom:80px}.project:first-of-type{margin-top:94px}.project:nth-child(odd){flex-direction:row-reverse}.project__thumbnail{width:45%;margin:0}.project__info{width:45%;padding-bottom:50px}}@media (min-width: 992px){.project{max-width:1015px}.project:nth-child(even){margin-right:auto}.project:nth-child(odd){margin-left:auto}.project__thumbnail{width:55%;margin:0}.project__info{align-self:stretch;display:flex;flex-direction:column;justify-content:center;align-items:flex-start;text-align:left;width:35%;padding-bottom:0}.project__name{margin:0}}

.info{margin-bottom:32px;padding-bottom:32px;border-top:1px solid rgba(0,0,0,0.2);border-bottom:1px solid rgba(0,0,0,0.2)}.info__social{display:inline-block}.info__social:first-child{margin-right:10px}@media (min-width: 992px){.info{display:flex;justify-content:space-between;align-items:baseline;margin-top:94px}.info__divider{width:50%;display:flex;flex-direction:column}}

.form{margin-bottom:80px}.form__label,.form__input,.form__textarea{display:block;width:100%;border:none;outline:none;box-sizing:border-box}.form__label:focus,.form__input:focus,.form__textarea:focus{border:1px solid var(--grayishDarkBlue)}.form__label{font-style:normal;font-weight:bold;font-size:13px;line-height:30px;color:var(--grayishDarkBlue);margin-bottom:8px}.form__input{background-color:#e6e6e7;padding:9px 0 9px 16px;margin-bottom:24px}.form__textarea{background-color:#e6e6e7;height:96px;padding-top:9px;padding-left:16px;margin-bottom:24px}.error{border:1px solid var(--red)}::-webkit-input-placeholder{font-family:inherit;font-style:normal;font-weight:normal;font-size:13px;line-height:30px;color:#a7a7ab}:-ms-input-placeholder{font-family:inherit;font-style:normal;font-weight:normal;font-size:13px;line-height:30px;color:#a7a7ab}::placeholder{font-family:inherit;font-style:normal;font-weight:normal;font-size:13px;line-height:30px;color:#a7a7ab}@media (min-width: 992px){.form{display:flex;justify-content:space-between;align-items:baseline}.form__form{width:50%}}

.navigation{position:relative;display:flex;justify-content:space-between;align-items:center;margin-top:32px;margin-bottom:40px}.navigation__links{position:absolute;right:0;top:calc(100% + 24px);display:flex;flex-direction:column;justify-content:center;align-items:center;width:223px;height:186px;background-color:var(--grayishDarkBlue)}.navigation__links--closed{display:none}.navigation__link{text-decoration:none;font-style:normal;font-weight:normal;font-size:12px;line-height:14px;text-align:center;letter-spacing:2px;text-transform:uppercase;color:#ffffff;padding:10px 0;margin:5px 0}@media (min-width: 768px){.navigation__icon{display:none}.navigation__links{position:static;position:initial;display:block;height:auto;width:auto;width:initial;background-color:transparent;background-color:initial}.navigation__link{color:var(--grayishDarkBlue);margin-right:42px}.navigation__link:last-child{margin-right:0}}

.footer{width:100%;background-color:var(--grayishDarkBlue);text-align:center;padding:56px 0}.footer__links{display:flex;flex-direction:column}.footer__link{text-decoration:none;font-style:normal;font-weight:normal;font-size:12px;line-height:14px;text-align:center;letter-spacing:2px;text-transform:uppercase;color:#ffffff;padding:10px 0;margin:5px 0}.footer__socials{margin-top:10px}.footer__social{display:inline-block}.footer__social:first-child{margin-right:10px}@media (min-width: 768px){.footer{padding:24px 0}.footer__container{width:90%;margin:0 auto;display:flex;justify-content:space-between;align-items:center}.footer__links{flex-direction:row;margin-right:auto;margin-left:48px}.footer__link{margin-right:42px}.footer__socials{margin:0}}@media (min-width: 992px){.footer__container{max-width:1100px}}

